import Vue from 'vue'
import App from './App'
import router from './router'
Vue.config.productionTip = false

import {store} from './store'

const echarts = require('echarts')
Vue.prototype.$echarts = echarts

new Vue({
  el: '#app',
	store,
  router,
  render: h => h(App)
})



