<template>
  <div class="write">
		<header>
			<span class="nav-icon">导航</span>
			<div class="title">
				<input 
					type="search" 
					v-model="article.title" 
					placeholder="博客标题" 
					@input="titleCount = article.title.length"
					maxlength="100"
					spellcheck="false"
				>
				<span class="title-count">{{titleCount}}/100</span>
			</div>
			<button class="btn save">保存草稿</button>
			<button class="btn issue">发布博客</button>
		</header>
		<div class="baseInfo">
			<div class="left">
				<input type="search" placeholder="作者" spellcheck="false" v-model="article.author">
				<input type="search" placeholder="分类" spellcheck="false" v-model="article.categeroy">
				<input type="search" placeholder="标签" spellcheck="false" v-model="article.tags">
			</div>
			<textarea class="right" placeholder="摘要简介" v-model="article.brief"></textarea>
		</div>
	</div>
</template>

<script>
export default {
  data() {
    return {
			titleCount : 0,
			article : {
				title : "",
				author : "yujinlong",
				categeroy : "",
				tags : "",
				brief : ""
			}
		}
  },
  methods: {
		
	},
	beforeRouteEnter(to,from,next) {
		next(vm => {
			vm.$store.state.hideNav = true
			window.onclick = (e) => {
			  if (!document.querySelector('.write .nav-icon').contains(e.target)){
			    vm.$store.state.hideNav = true
			  }
			  else
			    vm.$store.state.hideNav = false
			}
		})
	},
	beforeRouteLeave (to,from,next){
		window.onclick = ""
		next()
	}
}
</script>

<style>
.write{
	margin: 0;
}

.write .nav-icon{
	cursor: pointer;
}

.write header{
	background-color: #F4F4F4;
	padding: 10px;
	width: 100%;
	display: flex;
	align-items: center;
}

.write header .title{
	margin: 0 10px;
	flex: 1;
	display: flex;
	align-items: center;
	background-color: #FFFFFF;
	border: var(--border1);
	border-radius: 5px;
	padding: 0 5px;
}
.write header .title input{
	padding: 5px;
	border: none;
	flex: 1;
}
.write header .title input:focus{
	box-shadow: none;
}
.write header .title .title-count{
	font-size: 1.2em;
	margin: 0 5px;
	color: var(--green2);
}
.write header .btn{
	align-self: stretch;
	margin: 0 5px;
	transition: var(--hover-speed);
}
.write header .btn.issue{
	background-color: var(--blue);
}
.write header .btn:hover{
	box-shadow: none;
	transform: scale(1.1);
}

.write .baseInfo{
	margin: 10px 0;
	display: flex;
}
.write .baseInfo .left{
	padding: 10px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
}
.write .baseInfo .left input{
	width: 300px;
	padding: 5px;
}
.write .baseInfo .right{
	margin-right: 20px;
	flex: 1;
	height: 200px;
	resize: none;
	padding: 5px;
}
</style>


